<template>
  <div class="container mt-5">
    <b-row class="align-items-center">
      <b-col></b-col>
      <b-col class="text-center">
        <h2 class="mt-5">ÎÇ¥ Í∑∏Î£π Î™©Î°ù</h2>
      </b-col>
      <b-col class="text-right">
        <b-button 
          @click="moveToSavePage()" 
          squared 
          type="submit" 
          variant="primary">
          Í∑∏Î£π ÏÉùÏÑ±ÌïòÍ∏∞
        </b-button>
      </b-col>
    </b-row>
    <ul class="list-group mt-5" v-if="this.groups">
      <li
        v-for="(group, index) in groups"
        :key="group.id"
        class="list-group-item d-flex justify-content-between align-items-center"
        @click="loadMembers(group)"
      >
        <!-- Í∑∏Î£π Ïù¥Î¶ÑÍ≥º ÌÜ†Í∏Ä ÏïÑÏù¥ÏΩò -->
        <div @click="toggleGroup(group, index)" class="w-100 d-flex justify-content-between align-items-center">
          <span>{{ group.groupName }}</span>
          <div>
            <i :class="activeGroup === index ? 'bi bi-chevron-up' : 'bi bi-chevron-down'"></i>
            <!-- X Î≤ÑÌäº (ÏÇ≠Ï†ú) -->
            <b-button 
              class="btn btn-success btn-sm ms-2 delete-button"
              @click.stop="getNewMember(group)"
              >
              +
            </b-button>
            <button
              class="btn btn-danger btn-sm ms-2 delete-button"
              @click.stop="deleteGroup(group)"
            >
              ‚úï
            </button>
          </div>
        </div>

        <!-- Í∑∏Î£π Î©§Î≤Ñ Í¥ÄÎ¶¨ Í∏∞Îä• (ÌéºÏπ®/Îã´Ìûò) -->
        <div :id="'group-' + index" class="collapse" :class="{ show: activeGroup === index }">
          <div class="card mt-3">
            <div class="card-body">
              <ul class="list-group">
                <li v-for="member in members" :key="member.id" class="list-group-item">
                  {{ member.nickname }}
                </li>
              </ul>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      activeGroup: null, // ÌòÑÏû¨ Ïó¥Î¶∞ Í∑∏Î£πÏùò Ïù∏Îç±Ïä§
      groups: [
        {id:1,
          groupName:"testGroupName"
        }
      ],
      members: [
        { id: 2,
          nickname: "testMemberName"
        }
      ]
    };
  },
  created(){
    this.loadGroups()
  },
  methods: {
    async loadGroups(){
      try{
      const response = await axios.get('http://localhost:8080/myGroup');
      console.log(response.data)
      this.groups = response.data
      }
      catch (error) {
        console.error(error);
      }
    },
    loadMembers(group){
      console.log("Í∑∏Î£π: ",group)
      axios.get(`http://localhost:8080/getGroupMembers/${group.id}`)
      .then(response => {
        console.log("Í∑∏Î£π Î∂àÎü¨Ïò§Í∏∞ Í≤∞Í≥º: ",response.data);
        this.members = response.data;
      })
      .catch(error => {
        console.error(error);
      });
    },
    toggleGroup(group, index) {
      console.log(group)
      this.activeGroup = this.activeGroup === index ? null : index; // Í∑∏Î£π Ïó¥Í≥† Îã´Í∏∞
    },
    deleteGroup(group) {
      console.log(group)
      if (confirm("Í∑∏Î£πÏùÑ ÎÇòÍ∞ÄÏãúÍ≤†ÏäµÎãàÍπå?")) {
        axios
          .delete(`http://localhost:8080/leaveGroup/${group.id}`)
          .then(() => {
            console.log('ÌÉàÌá¥ÎêòÏóàÏäµÎãàÎã§.');
            alert('ÌÉàÌá¥ÎêòÏóàÏäµÎãàÎã§.');
            this.loadGroups();
          })
          .catch((error) => {
            console.error("Error deleting:", error);
          });
        }

    },
    getNewMember(groupId, memberId) {
      console.log("groupId: ",groupId," memberId: ",memberId)
    },
  },
};
</script>

<style scoped>
.list-group-item {
  cursor: pointer;
}
.delete-button {
  border: none;
  color: white;
  font-size: 1.2rem;
  line-height: 1;
  cursor: pointer;
}
li {
  display: inline-block !important;
  margin-right: 10px !important;
}
</style>
